import axios from "axios";
 
const fetchUrl = axios.create({
  baseURL: "https://pixabay.com/api/",
  params: {
    key: "20962496-895dd0bdb01e3709ca8fdfb85"
  }
})

 const getPage = (q, page, per_page) => {
  return fetchUrl.get("", {
    params: {
      q,
      page,
      per_page
    }
  })
}
export default getPage