import React, { Component,createRef } from 'react';
import SearchBar from './Components/SearchBar'
import getImage from '../src/Services/images'
import ImageGallery from './Components/ImageGallery/ImageGallery'
import Button from './Components/Button/Button'
import Loader from './Components/Loader/Loader'
import Modal from './Components/Modal/Modal'
class ImageFinder extends Component{
  state = {
    images: [],
    page: 1,
    loading: false,
    query: "",
    limit: 12,
    error: null,
    showModal: false,
    src:''
  }
  listref = createRef()
  componentDidMount() {
    this.fetchImg()
  }
  
  // openModal = ((e) => {
  //   this.setState(({ showModal }) => {
  //     return {
  //       showModal:true
  //     }
  //   })
  //   const img = this.state.images.find(image => image.id === Number(e.target.id))
  //   return {
  //     url:img.largeImageURL
  //   }
  // })
  componentDidUpdate(prevProp, prevState, snapshot) {
    const { loading } = this.state
    if (loading) {
      this.fetchImg()
      window.scrollTo({
        top: this.listref.current.clientHeight - snapshot,
        behavior: "smooth"
      })
    }
  }
  toggleModal = (src) => {
    this.setState(({ showModal }) => {
      return {
        showModal: !showModal,
        src
      }
    })
  }
  fetchImg = () => {
    const { page, limit, query } = this.state
    const fetch = getImage(query, page, limit)
    fetch.then(({ data }) => this.setState(({ images }) => {
      const imgFirst = [...images, ...data.hits];
      return {
        images: imgFirst,
        loading:false
      }
    }))
  }

  serchImg = query => {
    this.setState({
      images: [],
      loading: true,
      query
    })
  }

  onLoadMore = (e) => {
    e.preventDefault()
    this.setState(({ page }) => {
      return {
        loading:true,
        page: page + 1
      }
    })
        console.log(this.state)
  }
  render() {
    const {serchImg,onLoadMore,toggleModal} = this
    const { images,loading,showModal,src} = this.state

    return (
      <>
        <SearchBar onSubmit={serchImg} />
        <div ref={this.listref}>
          <ImageGallery  modalOpen={toggleModal} image={images} />
          </div>
        {loading && <Loader/>}
        <Button onClick={onLoadMore} title={"Загрузить еще"} />
        {showModal && <Modal showModal={toggleModal}>
          <img style={{width:'70vw',height:'90vh'}} src={src}/>
        </Modal>}
      </>
    )
  }
}
export default ImageFinder