import React from 'react';
import styles from './ImageGalleryItem.module.css'
const ImageGalleryItem = ({ image,modalOpen }) => {
  const loadImg = image.map((imgInfo, index) =>
    <li key={index} className={styles.ImageGalleryItem}>
      <img onClick={() => modalOpen(imgInfo.largeImageURL)} id={imgInfo.id}
        className={styles.ImageGalleryItemimage}
        src={imgInfo.webformatURL}
        alt={imgInfo.title} />
    </li>)
  return (
    loadImg
  )
}
export default ImageGalleryItem;